import React, { useCallback } from 'react';

import { RadioButtonGroup } from '../../Radio';
import FilterWrapper from '../FilterWrapper';
import { FilterBaseProps } from '../types';
import { TEXT_LIMIT_WITH_ELLIPSIS } from '../constants';

import styles from './styles.scss';

const FilterRadio: React.FC<FilterBaseProps> = ({
  config: {
    title,
    items
  },
  activeItems,
  isOpen: isControlledOpen,
  isHiddenHeader,
  zIndex,
  onChange,
  onOpenStatusChange
}) => {
  const handleClearAll = useCallback(
    () => {
      if (onChange) {
        onChange([]);
      }
    },
    [onChange]
  );

  const handleChange = useCallback((value: string) => {
    const newActiveItem = items.find(item => item.value === value);

    onChange(newActiveItem ? [newActiveItem] : []);
  }, [items, onChange]);

  return (
    <FilterWrapper
      title={ title }
      activeCount={ activeItems.length }
      isOpen={ isControlledOpen }
      isHiddenHeader={ isHiddenHeader }
      zIndex={ zIndex }
      onOpenStatusChange={ onOpenStatusChange }
      onClearAll={ handleClearAll }
    >
      <div className={ styles.container }>
        { items.length ? (
          <RadioButtonGroup
            name={ String(items[0]?.filterId ?? '') }
            items={ items.map(item => ({ ...item, disabled: false })) }
            activeValue={ activeItems[0]?.value }
            onChange={ handleChange }
            textLimitWithEllipsis={ TEXT_LIMIT_WITH_ELLIPSIS }
          />
        ) : null }
      </div>
    </FilterWrapper>
  );
};

export default FilterRadio;
